article {
  width: 50%;
  align-self: start;
  font-size: inherit;
  overflow: scroll;
  height: 100vh;
}

article::-webkit-scrollbar {
  width: 0;
  height: 0;
}

article * {
  margin: 1rem;
  opacity: 0;
  transition: 1s;
}

article img {
  width: 100%;
}

article ul li {
  opacity: 1;
  margin: 0 1rem;
}
